define('components/TestComponent/TestComponent',['vue', 'text!./template.html', 'css!./style.css'], function(Vue, template, style) {
  function init(el, data) {
    data.newitemlabel = '';

    return new Vue({
      el: el,
      template: template,
      data: function() {
        return data
      },
      methods: {

      addItem: function(evt){
        this.items.push(this.newitemlabel);
      },
      removeItem: function(itm){
        this.items.splice(itm, 1);
      }
    }
    })
  }
  return {
    init: init
  }
});

